@import url('https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c&family=Montserrat&display=swap');

body {
  margin: 0;
  font-family: Montserrat,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.sidePanel{

  position: absolute;
  width: 280px;
  height: 720px;
  left: -260px;
  background-color: rgb(30, 30, 44);
  border-radius: 5px;
  box-shadow: 0px 2px 12px rgb(1, 0, 10);
  transition:  500ms;
}
.sidePanel:hover{
  left:-20px;
}
.right{  
  left: auto;
  right: -260px;
}
.right:hover{
  left: auto;
  right: -20px;  
}